    left: -5000vw;
    background-color: white;
    opacity: 0.2;
    z-index: -1;
  } */
}
.nodesystem-transform > svg {
  position: absolute;
  pointer-events: none;
  overflow: visible;
  width: 1px !important;
  height: 1px !important;
  left: 50%;
  top: 50%;
  z-index: 0;
}
.nodesystem-transform > svg > path {
  stroke-linecap: round;
}

.nodesystem-wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-image: radial-gradient(rgba(255, 255, 255, 0.1) 1.2px, transparent 1.2px), radial-gradient(rgba(255, 255, 255, var(--scale)) 1px, transparent 1px);
  background-position: var(--pos-x) var(--pos-y), var(--pos-x) var(--pos-y);
  background-size: calc(var(--scale-x) + calc(var(--scale-x) / 4)) calc(var(--scale-y) + calc(var(--scale-y) / 4)), calc(var(--scale-x) / 4) calc(var(--scale-y) / 4);
}
.nodesystem-wrapper.nodesystem-state-dragging {
  cursor: grab;
}